import React from "react";
import {  makeStyles } from "@material-ui/core/styles";
import AppBar from "@material-ui/core/AppBar";
import Toolbar from "@material-ui/core/Toolbar";
import IconButton from "@material-ui/core/IconButton";
import Badge from "@material-ui/core/Badge";
import MenuItem from "@material-ui/core/MenuItem";
import Menu from "@material-ui/core/Menu";
import AccountCircle from "@material-ui/icons/AccountCircle";
import NotificationsIcon from "@material-ui/icons/Notifications";
import MoreIcon from "@material-ui/icons/MoreVert";
import Icon from "@material-ui/core/Icon";
import Dialog from "@material-ui/core/Dialog";
import { store } from "./redux";
import DialogActions from "@material-ui/core/DialogActions";
import DialogContent from "@material-ui/core/DialogContent";
import DialogContentText from "@material-ui/core/DialogContentText";
import DialogTitle from "@material-ui/core/DialogTitle";
import Button from "@material-ui/core/Button";
import socket from "./sockets";

import { acceptedfriendrequest, rejectedfriendrequest } from "./api";
import { useHistory } from "react-router-dom";
const useStyles = makeStyles((theme) => ({
  grow: {
    flexGrow: 1,
  },
  menuButton: {
    marginRight: theme.spacing(2),
  },
  title: {
    display: "none",
    [theme.breakpoints.up("sm")]: {
      display: "block",
    },
  },

  searchIcon: {
    padding: theme.spacing(0, 2),
    height: "100%",
    position: "absolute",
    pointerEvents: "none",
    display: "flex",
    alignItems: "center",
    justifyContent: "center",
  },
  inputRoot: {
    color: "inherit",
  },
  inputInput: {
    padding: theme.spacing(1, 1, 1, 0),
    paddingLeft: `calc(1em + ${theme.spacing(4)}px)`,
    transition: theme.transitions.create("width"),
    width: "100%",
    [theme.breakpoints.up("md")]: {
      width: "20ch",
    },
  },
  sectionDesktop: {
    display: "none",
    [theme.breakpoints.up("md")]: {
      display: "flex",
    },
  },
  sectionMobile: {
    display: "flex",
    [theme.breakpoints.up("md")]: {
      display: "none",
    },
  },
}));

export default function PrimarySearchAppBar(props) {
  const classes = useStyles();
  const history = useHistory();
  const [requests, setrequests] = React.useState(props.requests);
  const [anchorEl, setAnchorEl] = React.useState(null);
  const [ranchorEl, setrAnchorEl] = React.useState(null);
  const [mobileMoreAnchorEl, setMobileMoreAnchorEl] = React.useState(null);
  const isMenuOpen = Boolean(anchorEl);
  const isrMenuOpen = Boolean(ranchorEl);
  const isMobileMenuOpen = Boolean(mobileMoreAnchorEl);
  const [dialogcontext, setcontext] = React.useState("");
  const [opendialog, setdialog] = React.useState(false);
  const handledialogopen = () => {
    setdialog(true);
  };
  const handleClosedialog = () => {
    setdialog(false);
  };
  const handleProfileMenuOpen = (event) => {
    setAnchorEl(event.currentTarget);
  };
  const handlerequestsOpen = (event) => {
    setrAnchorEl(event.currentTarget);
  };
  const handlerequestClose = (event) => {
    setrAnchorEl(null);
  };
  const handleMobileMenuClose = () => {
    setMobileMoreAnchorEl(null);
  };

  const handleMenuClose = () => {
    setAnchorEl(null);
    handleMobileMenuClose();
  };

  const handleMobileMenuOpen = (event) => {
    setMobileMoreAnchorEl(event.currentTarget);
  };

  React.useEffect(() => {

    setrequests(props.requests);
  }, [props.requests]);

  const menuId = "primary-search-account-menu";

  const requestsMenu = (
    <Menu
      anchorEl={ranchorEl}
      anchorOrigin={{ vertical: "top", horizontal: "right" }}
      id={menuId}
      keepMounted
      transformOrigin={{ vertical: "top", horizontal: "right" }}
      open={isrMenuOpen}
      onClose={handlerequestClose}
    >
      {requests.map((req, index) => {
        return (
          <MenuItem key={index} onClick={handleMenuClose}>
            {req.adder}{" "}
            <Icon
              onClick={() => {
                acceptedfriendrequest(req.adder, store.getState().username)
                  .then((res) => {
                    setcontext(res.data);
                    let i = requests.filter((reqs) => reqs.adder !== req.adder);
                    socket.emit("addthedude", {
                      adder: req.adder,
                      me: store.getState().username,
                    });
                    handledialogopen();
                    setrequests(i)

                  })
                  .catch((err) => {
                    setcontext(err.response.data);
                    handledialogopen();
                  });
              }}
              style={{ backgroundColor: "green", marginLeft: "20px" }}
            >
              check_circle
            </Icon>{" "}
            <Icon
              onClick={() => {
                rejectedfriendrequest(req.adder, store.getState().username)
                  .then(() => {
                    let i = requests.filter((reqs) => reqs.adder !== req.adder);
                    setrequests(i);
                  })
                  .catch(() => {});
              }}
              style={{ backgroundColor: "red", marginLeft: "10px" }}
            >
              cancel
            </Icon>
          </MenuItem>
        );
      })}
    </Menu>
  );

  const renderMenu = (
    <Menu
      anchorEl={anchorEl}
      anchorOrigin={{ vertical: "top", horizontal: "right" }}
      id={menuId}
      keepMounted
      transformOrigin={{ vertical: "top", horizontal: "right" }}
      open={isMenuOpen}
      onClose={handleMenuClose}
    >
      <MenuItem
        onClick={() => {
          socket.emit("LoggedOut", store.getState());
          history.push("/");
        }}
      >
        Logout
      </MenuItem>
    </Menu>
  );

  const mobileMenuId = "primary-search-account-menu-mobile";
  const renderMobileMenu = (
    <Menu
      anchorEl={mobileMoreAnchorEl}
      anchorOrigin={{ vertical: "top", horizontal: "right" }}
      id={mobileMenuId}
      keepMounted
      transformOrigin={{ vertical: "top", horizontal: "right" }}
      open={isMobileMenuOpen}
      onClose={handleMobileMenuClose}
    >
      <MenuItem onClick={handlerequestsOpen}>
        <IconButton color="inherit">
          <Badge badgeContent={requests.length} color="secondary">
            <NotificationsIcon />
          </Badge>
        </IconButton>
        <p>Notifications</p>
      </MenuItem>
      <MenuItem onClick={handleProfileMenuOpen}>
        <IconButton
          aria-label="account of current user"
          aria-controls="primary-search-account-menu"
          aria-haspopup="true"
          color="inherit"
        >
          <AccountCircle />
        </IconButton>
        <p>Profile</p>
      </MenuItem>
    </Menu>
  );

  return (
    <div className={classes.grow}>
      <AppBar position="static" style={{ backgroundColor: "#001a34" }}>
        <Toolbar>
          <img  alt='no pic' src={require("./logo.png")} style={{ height: "60px" }} />
          <span style={{marginLeft:'10px'}}>Logged in as {store.getState().username}</span>
          <div className={classes.grow} />
          <div className={classes.sectionDesktop}>
            <IconButton color="inherit" onClick={handlerequestsOpen}>
              <Badge badgeContent={requests.length} color="secondary">
                <NotificationsIcon />
              </Badge>
            </IconButton>
            <IconButton
              edge="end"
              aria-label="account of current user"
              aria-controls={menuId}
              aria-haspopup="true"
              onClick={handleProfileMenuOpen}
              color="inherit"
            >
              <AccountCircle />
            </IconButton>
          </div>
          <div className={classes.sectionMobile}>
            <IconButton
              aria-label="show more"
              aria-controls={mobileMenuId}
              aria-haspopup="true"
              onClick={handleMobileMenuOpen}
              color="inherit"
            >
              <MoreIcon />
            </IconButton>
          </div>
        </Toolbar>
      </AppBar>
      {renderMobileMenu}
      {renderMenu}
      {requestsMenu}

      <Dialog
        open={opendialog}
        onClose={handleClosedialog}
        aria-labelledby="alert-dialog-title"
        aria-describedby="alert-dialog-description"
      >
        <DialogTitle id="alert-dialog-title">{"Alert"}</DialogTitle>
        <DialogContent>
          <DialogContentText>{dialogcontext}</DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button onClick={handleClosedialog} color="primary" autoFocus>
            Continue
          </Button>
        </DialogActions>
      </Dialog>
    </div>
  );
}
