import React, { useState, useRef } from "react";
import "./login.css";
import { Button } from "@material-ui/core";
import { Input } from "@material-ui/core";
import { useHistory, Link } from "react-router-dom";
import Dialog from "@material-ui/core/Dialog";
import DialogActions from "@material-ui/core/DialogActions";
import DialogContent from "@material-ui/core/DialogContent";
import DialogContentText from "@material-ui/core/DialogContentText";
import DialogTitle from "@material-ui/core/DialogTitle";
import CircularProgress from "@material-ui/core/CircularProgress";
import { login } from "./api";
import setReduxUser from "./redux_actions";
import { store } from "./redux";
export default function Login() {
  const [user, setuser] = useState("");
  const [pass, setpass] = useState("");
  const [open, setOpen] = React.useState(false);
  const [info, setInfo] = useState();
  const spinner = useRef("spinner");
  const lgnbtn = useRef("lgnbtn");
  const history = useHistory();
  const handleClickOpen = (text) => {
    setInfo(text);
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };
  let loginapp = () => {
    if (user === "") return handleClickOpen("Please enter a valid username");
    if (pass === "" || pass.length < 5)
      return handleClickOpen("Password must be atleast 5 characters");
    spinner.current.style.display = "";
    lgnbtn.current.style.display = "none";

    login(user, pass)
      .then((res) => {
        store.dispatch(
          setReduxUser({
            username: res.data.username,
            user_id: res.data.user_id,
            isLoggedin: true,
          })
        );
        history.push("/audiocord");
      })
      .catch((err) => {
        if (err.response) {
          handleClickOpen(err.response.data);
        }

        spinner.current.style.display = "none";
        lgnbtn.current.style.display = "";
      });
  };
  return (
    <div
      className="mainlogin"
      style={{
        display: "flex",
        flexDirection: "column",
        alignItems: "center",
        justifyContent: "center",
      }}
    >
      <img alt='nopic' src={require("./logo.png")} />
      <Input onChange={(e) => setuser(e.target.value)} placeholder="USERNAME" />
      <Input
        style={{ marginTop: "10px" }}
        type="password"
        onChange={(e) => setpass(e.target.value)}
        placeholder="PASSWORD"
      />
      <Button
        onClick={loginapp}
        ref={lgnbtn}
        style={{ marginTop: "10px", color: "white", background: "#001a34" }}
      >
        LOGIN
      </Button>
      <CircularProgress
        ref={spinner}
        style={{ marginTop: "10px", color: "#001a34", display: "none" }}
      />
      <Link to="/register" color="inherit" style={{ marginTop: "10px" }}>
        Register now
      </Link>

      <Dialog
        open={open}
        onClose={handleClose}
        aria-labelledby="alert-dialog-title"
        aria-describedby="alert-dialog-description"
      >
        <DialogTitle id="alert-dialog-title">{"Alert"}</DialogTitle>
        <DialogContent>
          <DialogContentText>{info}</DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button onClick={handleClose} color="primary" autoFocus>
            Continue
          </Button>
        </DialogActions>
      </Dialog>
    </div>
  );
}
