import React, { useState, useEffect, useRef } from "react";
import Nav from "./Nav";
import "./login.css";
import { Button } from "@material-ui/core";
import { Input } from "@material-ui/core";
import Peer from "simple-peer";
import { makeStyles } from "@material-ui/core/styles";
import GridList from "@material-ui/core/GridList";
import GridListTile from "@material-ui/core/GridListTile";
import { store } from "./redux";
import { friendrequest, getfriends } from "./api";
import Dialog from "@material-ui/core/Dialog";
import DialogActions from "@material-ui/core/DialogActions";
import DialogContent from "@material-ui/core/DialogContent";
import DialogContentText from "@material-ui/core/DialogContentText";
import DialogTitle from "@material-ui/core/DialogTitle";
import GridListTileBar from "@material-ui/core/GridListTileBar";
import IconButton from "@material-ui/core/IconButton";
import InfoIcon from "@material-ui/icons/Call";
import socket from "./sockets";
const useStyles = makeStyles((theme) => ({
  root: {
    display: "flex",
    flexWrap: "wrap",
    justifyContent: "space-around",
    overflow: "hidden",
    backgroundColor: theme.palette.background.paper,
  },
  gridList: {
    width: 500,
    height: 450,
  },
  icon: {
    color: "rgba(255, 255, 255, 0.54)",
  },
}));

export default function Audiocord(props) {
  const [user, setuser] = useState("");
  const [info, setInfo] = useState();
  const [open, setOpen] = React.useState(false);
  const [friends, setfriends] = useState([]);
  const [requests, setrequests] = useState([]);
  const classes = useStyles();
  const userAudio = useRef();
  const callerAudio=useRef();
  const [ring]=useState(new Audio(require('./ringing.mp3')));
  const [incomingCalll]= useState(new Audio(require("./promise.mp3")))
  const [callAccepted, setcallAccepted] = useState(false);
  const [recievingCall, setrecievingCall] = useState(false);
  const [Caller, setCaller] = useState();
  const [CallerSignal, setCallerSignal] = useState();
  const [startcall, setcallstart] = useState(false)
  const [stream, setstream] = useState();
  const [guyimcalling, setguy] = useState()
  const handleClickOpen = (text) => {
    setInfo(text);
    setOpen(true);
  };
  useEffect(() => {
    getfriends(store.getState().username)
      .then((res) => {
        let friendlist = [];
        let requestlist = [];
        res.data.map((dude) => {
          if (
            dude.reciever === store.getState().username &&
            dude.isFriend === 0
          ) {
            requestlist.push(dude);
          } else if (dude.isFriend === 1) {
            friendlist.push(dude);
          }
        });
        let myfriends = [];
        let me = store.getState().username;
        friendlist.map((friend) => {
          if (friend.adder === me) {
            return myfriends.push({
              myfriend: friend.reciever,
              friend_id: friend.user_friend,
            });
          } else {
            return myfriends.push({
              myfriend: friend.adder,
              friend_id: friend.user,
            });
          }
        });
        setfriends(myfriends);
        setrequests(requestlist);
      })
      .catch(() => {});
  }, [friends.length]);

  useEffect(() => {
    socket.on('callcancel',()=>{
    setrecievingCall(false);
    });
    socket.on('endedcall',()=>{
      setcallstart(false);
      setCallerSignal(null);
      setCaller(null);
      setcallAccepted(false);
      setguy(null);
    
      
    })
  socket.on('stopringingpls',()=>{
    setrecievingCall(true);
    incomingCalll.pause();
  })
    socket.on("otherGuyBusy",()=>{
    handleClickOpen('User Busy..');
    ring.pause();
    })
    socket.on('starttimer',()=>{
      setcallstart(true);
      handleClose();
      setrecievingCall(false);
    
    
    })
    socket.on('inacall',()=>{
      handleClickOpen('User is in another call..');
      ring.pause();
    })
    socket.on("userOffline", () => {
    handleClickOpen('This user is currently offline');
    ring.pause();
    });
    socket.emit("LoggedIn", { username: store.getState().username });
    socket.on("NewFriendRequest", (data) => {
      let array = requests;
      array.push(data);
      setrequests(array);
    });
 
    navigator.mediaDevices.getUserMedia({audio: true}).then((stream) => {
      setstream(stream);
      
      if(userAudio.current){
        userAudio.current.srcObject=stream;
      }
    });
    socket.on("addhimnow", (data) => {
      let obj = { myfriend: data.friend };
      let x = friends;
      x.push(obj);
      setfriends(x);
    });
    socket.on("hey", (data) => {
        setCaller(data.from);
        setCallerSignal(data.signal);
    });
  }, []);
useEffect(()=>{
  if(recievingCall===false){
    incomingCalll.pause();
  }


},[recievingCall])
  let acceptCall = () => {
    setrecievingCall(false);

    setcallAccepted(true);
    const peer = new Peer({
      initiator: false,
      trickle: false,
      stream: stream,
    });

    peer.on("signal", (data) => {
      socket.emit("acceptCall", {
        signal: data,
        from: Caller,
      });
    });
    peer.on("stream", (stream) => {
      if(callerAudio.current){
         callerAudio.current.srcObject=stream;
      }
    });
    peer.signal(CallerSignal);
    

    
  };
  let declineCall=()=>{
    setcallAccepted(false);
    socket.emit("declinedCall",{from:Caller})
    setrecievingCall(false);
    incomingCalll.pause();

  };
  let incomingCall;
  if (recievingCall) {
   
    incomingCalll.play();

    incomingCall = (
      <div>
        <Dialog open={recievingCall}>
          <DialogTitle id="alert-dialog-title">{"Incoming Call"}</DialogTitle>
          <DialogContent>
            <DialogContentText>Call from {Caller}</DialogContentText>
          </DialogContent>
          <DialogActions>
            <Button onClick={acceptCall} color="primary" autoFocus>
              Accept
            </Button>
            <Button onClick={declineCall}color="primary" autoFocus>
              Decline
            </Button>
          </DialogActions>
        </Dialog>
      </div>
    );
  }let userVideo;
  if(stream){
 userVideo=(
  <video autoPlay muted playsInline ref={userAudio} />

 )
  }
  let partnerVideo;
  if(callAccepted){
    partnerVideo=(
      <video  autoPlay  playsInline ref={callerAudio} />

    )
  }
  const handleClose = () => {
    setOpen(false);
  };
  let searchforuser = () => {
    let fromuser = store.getState().username;
    let touser = user;
    socket.emit("SendFriendRequest", { fromuser, touser });
    friendrequest(fromuser, touser)
      .then(() => {
        handleClickOpen(`friend request sent to ${touser}`);
      })
      .catch((err) => {
        handleClickOpen(err.response.data);
      });
  };
  const exitCall=()=>{
    if(Caller){
      socket.emit('endcall',{to:Caller});

    }else{
      socket.emit('endcall',{to:guyimcalling});

    }
    setcallstart(false);
    setCallerSignal(null);
    setCaller(null);
    setcallAccepted(false);
    setguy(null);
   

  }
  const mutemic=()=>{
    stream.getAudioTracks()[0].enabled=!stream.getAudioTracks()[0].enabled;
  }
  const CancelCall=()=>{
    ring.pause();
    socket.emit('cancelledcall',{to:guyimcalling,from:store.getState().username});

    handleClose();
  };
  return (
    <div className="mainapp">
      <Nav requests={requests} />
      <div
        style={{
          marginTop: "10%",
          display: "flex",
          flexDirection: "column",
          alignItems: "center",
          justifyContent: "center",
        }}
      >
        <div
          style={{
            display: "flex",
            flexDirection: "row",
            alignItems: "center",
            justifyContent: "center",
          }}
        >
          <Input
            onChange={(e) => setuser(e.target.value)}
            placeholder="SEARCH FOR A USER"
          />
          <Button
            style={{
              marginLeft: "10px",
              color: "white",
              background: "#001a34",
            }}
            onClick={searchforuser}
          >
            add friend
          </Button>
        </div>

        <GridList
          style={{ marginTop: "10px" }}
          cellHeight={160}
          className={classes.gridList}
          cols={3}
        >
          {friends.map((tile, index) => (
            <GridListTile key={index} cols={1}>
              <img alt='nopic' src={require("./user.svg")} />
              <GridListTileBar
                title={tile.myfriend}
                subtitle={<span>id: {tile.friend_id}</span>}
                actionIcon={
                  <IconButton
                    onClick={() => {
                      const peer = new Peer({
                        initiator: true,
                        trickle: false,
                        stream: stream,
                      });
                     setguy(tile.myfriend)
                      peer.on("signal", (data) => {
                        socket.emit("CallUser", {
                          userToCall: tile.myfriend,
                          signalData: data,
                          caller: store.getState().username,
                        });
                        
                      });
                      socket.emit('callimsodone',{userToCall:tile.myfriend});
                      peer.on("stream", (stream) => {
                        if(callerAudio.current)
                        {
                          callerAudio.current.srcObject=stream;
                        }
                      });
                      socket.on("callAccepted", (signal) => {
                        ring.pause();
                        setcallAccepted(true);
                        peer.signal(signal);
                      });
                      
                      ring.loop=true;
                      ring.play();
                      handleClickOpen(`Calling ${tile.myfriend} ..`);
                    }}
                    style={{ color: "green" }}
                  >
                    <InfoIcon />
                  </IconButton>
                }
              />
            </GridListTile>
          ))}
        </GridList>
      </div>

      <Dialog
        open={open}
        onClose={handleClose}
        aria-labelledby="alert-dialog-title"
        aria-describedby="alert-dialog-description"
      >
        <DialogTitle id="alert-dialog-title">{"Alert"}</DialogTitle>
        <DialogContent>
          <DialogContentText>{info}</DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button onClick={CancelCall} color="primary" autoFocus>
            Cancel Call
          </Button>
        </DialogActions>
      </Dialog>
      {incomingCall}
{userVideo}
{partnerVideo}
<div>
        <Dialog open={startcall}>
          <DialogTitle id="alert-dialog-title">{"Ongoing Call.."}</DialogTitle>
          <DialogContent>
            <DialogContentText>{Caller || guyimcalling}</DialogContentText>
            <DialogContentText>Have fun and don't be toxic :)</DialogContentText>

          </DialogContent>
          <DialogActions>
          <Button onClick={mutemic}color="primary" autoFocus>
             MUTE MIC
            </Button>
  
            <Button onClick={exitCall}color="primary" autoFocus>
              End Call
            </Button>
          </DialogActions>
        </Dialog>
      </div>
    </div>
  );
}
