import React,{useState,useRef} from 'react'
import './login.css'
import { Button } from '@material-ui/core';
import { Input } from '@material-ui/core';
import { Link, useHistory} from 'react-router-dom'
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import CircularProgress from '@material-ui/core/CircularProgress';
import {register as reg} from './api'
export default function Login() {
    const [user,setuser]=useState('');
    const [pass,setpass]=useState('');

    const [info, setInfo] = useState()
    const [open, setOpen] = React.useState(false);
    const spinner = useRef('spinner')
    const regbtn=useRef('regbtn')
    const history=useHistory();
    const handleClickOpen = (text) => {
        setInfo(text)
        setOpen(true);
      };
    
      const handleClose = () => {
        setOpen(false);
      };

    let register=()=>{
        if(user==='') return handleClickOpen('Please enter a valid username');
        if(pass==='' || pass.length<5) return handleClickOpen('Password must be atleast 5 characters');
        spinner.current.style.display=''
        regbtn.current.style.display='none'
        reg(user,pass).then((res)=>{
            handleClickOpen(`${res.data}, You will be redirected to login..`);
            setTimeout(() => {
                history.push("/");
            }, 3000);
            
        }).catch((err)=>{
           
            handleClickOpen(err.response.data);
            spinner.current.style.display='none';
            regbtn.current.style.display='';
            
        })
    }
    return (
        <div className="mainlogin" style={{display:'flex',flexDirection:'column',alignItems:'center',justifyContent:'center'}}>
        <img alt='no pic' src={require('./logo.png')}/>
         <Input onChange={(e)=>setuser(e.target.value)} placeholder="ENTER A USERNAME"/>
         <Input style={{marginTop:'10px'}} type="password" onChange={(e)=>setpass(e.target.value)} placeholder="ENTER A PASSWORD"/>

         <CircularProgress ref={spinner}  style={{marginTop:'10px',color:'#001a34',display:'none'}} />
         <Button ref={regbtn} onClick={register} style={{marginTop:'10px',color:'white',background:'#001a34'}}>Register</Button>    
         <Link style={{marginTop:'10px'}} to='/'>Back to Login</Link>
         <Dialog
        open={open}
        onClose={handleClose}
        aria-labelledby="alert-dialog-title"
        aria-describedby="alert-dialog-description"
      >
        <DialogTitle id="alert-dialog-title">{"Alert"}</DialogTitle>
        <DialogContent>
          <DialogContentText >
              {info}
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button onClick={handleClose} color="primary" autoFocus>
            Continue
          </Button>
        </DialogActions>
      </Dialog>
        </div>
    )
}
