import axios from 'axios';
axios.interceptors.request.use(
    function(config) {
      config.withCredentials = true;
      return config;
    },
    function(error) {
      return Promise.reject(error);
    }
  );
  
export function register(user,pass){
let obj={user,pass}
return axios.post('api/v1/register',obj);
}
export function login(user,pass){
    let obj={user,pass};
return axios.post('api/v1/login',obj);
}

export function friendrequest(from,to){
  let obj={from,to};
  return axios.post('comm/v1/addfriend',obj);
}
export function getfriends(user){

  return axios.get(`comm/v1/getallfriends/${user}`);
}

export function acceptedfriendrequest(adder,reciever){
  let obj={adder,reciever};
  return axios.post('comm/v1/acceptedfriend',obj);
}
export function rejectedfriendrequest(adder,reciever){
  let obj={adder,reciever};
  return axios.post('comm/v1/rejectedfriend',obj);
}